module.exports = "import React from 'react';\nimport PropTypes from 'prop-types';\nimport {withStyles} from '@material-ui/core/styles';\nimport Stepper from '@material-ui/core/Stepper';\nimport Step from '@material-ui/core/Step';\nimport StepLabel from '@material-ui/core/StepLabel';\nimport StepConnector from '@material-ui/core/StepConnector';\nimport Button from '@material-ui/core/Button';\nimport Typography from '@material-ui/core/Typography';\n\nconst styles = theme => ({\n    root              : {\n        width: '90%'\n    },\n    button            : {\n        marginRight: theme.spacing.unit\n    },\n    instructions      : {\n        marginTop   : theme.spacing.unit,\n        marginBottom: theme.spacing.unit\n    },\n    connectorActive   : {\n        '& $connectorLine': {\n            borderColor: theme.palette.secondary.main\n        }\n    },\n    connectorCompleted: {\n        '& $connectorLine': {\n            borderColor: theme.palette.primary.main\n        }\n    },\n    connectorDisabled : {\n        '& $connectorLine': {\n            borderColor: theme.palette.grey[100]\n        }\n    },\n    connectorLine     : {\n        transition: theme.transitions.create('border-color')\n    }\n});\n\nfunction getSteps()\n{\n    return ['Select campaign settings', 'Create an ad group', 'Create an ad'];\n}\n\nfunction getStepContent(step)\n{\n    switch ( step )\n    {\n        case 0:\n            return 'Select campaign settings...';\n        case 1:\n            return 'What is an ad group anyways?';\n        case 2:\n            return 'This is the bit I really care about!';\n        default:\n            return 'Unknown step';\n    }\n}\n\nclass CustomizedStepper extends React.Component {\n    state = {\n        activeStep: 0\n    };\n\n    handleNext = () => {\n        this.setState(state => ({\n            activeStep: state.activeStep + 1\n        }));\n    };\n\n    handleBack = () => {\n        this.setState(state => ({\n            activeStep: state.activeStep - 1\n        }));\n    };\n\n    handleReset = () => {\n        this.setState({\n            activeStep: 0\n        });\n    };\n\n    render()\n    {\n        const {classes} = this.props;\n        const {activeStep} = this.state;\n        const steps = getSteps();\n        const connector = (\n            <StepConnector\n                classes={{\n                    active   : classes.connectorActive,\n                    completed: classes.connectorCompleted,\n                    disabled : classes.connectorDisabled,\n                    line     : classes.connectorLine\n                }}\n            />\n        );\n\n        return (\n            <div className={classes.root}>\n                <Stepper activeStep={activeStep} connector={connector}>\n                    {steps.map(label => (\n                        <Step key={label}>\n                            <StepLabel>{label}</StepLabel>\n                        </Step>\n                    ))}\n                </Stepper>\n                <Stepper alternativeLabel activeStep={activeStep} connector={connector}>\n                    {steps.map(label => (\n                        <Step key={label}>\n                            <StepLabel>{label}</StepLabel>\n                        </Step>\n                    ))}\n                </Stepper>\n                <div>\n                    {activeStep === steps.length ? (\n                        <div>\n                            <Typography className={classes.instructions}>\n                                All steps completed - you&apos;re finished\n                            </Typography>\n                            <Button onClick={this.handleReset} className={classes.button}>\n                                Reset\n                            </Button>\n                        </div>\n                    ) : (\n                        <div>\n                            <Typography className={classes.instructions}>{getStepContent(activeStep)}</Typography>\n                            <div>\n                                <Button\n                                    disabled={activeStep === 0}\n                                    onClick={this.handleBack}\n                                    className={classes.button}\n                                >\n                                    Back\n                                </Button>\n                                <Button\n                                    variant=\"contained\"\n                                    color=\"primary\"\n                                    onClick={this.handleNext}\n                                    className={classes.button}\n                                >\n                                    {activeStep === steps.length - 1 ? 'Finish' : 'Next'}\n                                </Button>\n                            </div>\n                        </div>\n                    )}\n                </div>\n            </div>\n        );\n    }\n}\n\nCustomizedStepper.propTypes = {\n    classes: PropTypes.object\n};\n\nexport default withStyles(styles)(CustomizedStepper);\n"